'use client';

import { useAuth } from '@/src/context/AuthContext';

export default function CompliancePage() {
  const { user } = useAuth();
  return (
    <div className="space-y-6">
      <h1 className="text-2xl font-semibold">Compliance (KYC)</h1>
      <p className="text-muted-foreground">Verify your identity and upload required documents.</p>

      <div className="bg-white rounded-xl border p-6 max-w-xl">
        <div className="text-sm text-slate-600 mb-2">Current Status</div>
        <span className={`px-2 py-1 rounded-md text-xs ${user?.kycStatus === 'verified' ? 'bg-green-100 text-green-700' : user?.kycStatus === 'pending' ? 'bg-yellow-100 text-yellow-700' : 'bg-red-100 text-red-700'}`}>{user?.kycStatus || 'unverified'}</span>
        <p className="text-slate-500 mt-4 text-sm">KYC API endpoints are pending on backend. For now, status reflects your account setup.</p>
      </div>
    </div>
  );
}